<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div id="Dashboard">
        <v-fab-transition>
            <v-btn
                    key="add"
                    color="pink"
                    dark
                    fab
                    fixed
                    bottom
                    right
                    to="/new"
            >
                <v-icon>add</v-icon>
                <v-icon>close</v-icon>
            </v-btn>
        </v-fab-transition>
        <v-data-table
                class="elevation-1"
                :items="employees"
        >
            <template v-slot:items="props">
                <td><v-chip>{{props.item.dept}}</v-chip> {{props.item.employee_id}} : {{props.item.name}}</td>
                <td class="text-xs-right"> <router-link :to="{name:'view-employee',params:{employee_id: props.item.employee_id}}" style="text-decoration: none"> <v-icon>visibility</v-icon></router-link></td>
            </template>
        </v-data-table>

    </div>


</template>


<script>
    import db from './firebaseInit'

    export default {
        name: "Dashboard",
        data() {
            return {
                employees: []
            }
        },
        created() {
            db.collection('employees').get().then(
                querySnapshot => {
                    querySnapshot.forEach(doc => {
                        const data = {
                            'id': doc.id,
                            'employee_id': doc.data().employee_id,
                            'name': doc.data().name,
                            'dept': doc.data().dept,
                            'position' : doc.data().position
                        }
                        this.employees.push(data)
                    })
                }
            )
        }
    }
</script>


