<template>
    <div id="view-employee">
        <v-fab-transition>
            <v-btn
                    key="edit"
                    color="red"
                    dark
                    fab
                    fixed
                    bottom
                    right
                    :to="{name:'edit-employee',params:{employee_id}}"
            >
                <v-icon>edit</v-icon>
                <v-icon>close</v-icon>
            </v-btn>
        </v-fab-transition>
        <v-card color="blue-grey darken-2" class="white--text">
            <v-card-title primary-title>
                <div>
                    <div class="display-3">{{name}}</div>
                    <div class="title pa-2">Employee ID : {{employee_id}}</div>
                    <div class="title pa-2">Department : {{dept}}</div>
                    <div class="title pa-2">Position : {{position}}</div>
                </div>
            </v-card-title>
            <v-card-actions class="ml-2">
                <v-btn to="/" color="success" dark>Back</v-btn>
                <v-btn @click="deleteEmployee"  color="error">Delete</v-btn>
            </v-card-actions>
        </v-card>
    </div>
</template>

<script>
    import db from './firebaseInit'

    export default {
        name: "ViewEmployee",
        data() {
            return {
                employee_id: null,
                name: null,
                dept: null,
                position: null
            }
        },
        beforeRouteEnter(to, from, next) {
            db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(QuerySnapshot => {
                QuerySnapshot.forEach(doc => {
                    next(vm => {
                        vm.employee_id = doc.data().employee_id;
                        vm.name = doc.data().name;
                        vm.dept = doc.data().dept;
                        vm.position = doc.data().position
                    })
                })
            })
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData() {
                db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(QuerySnapshot => {
                    QuerySnapshot.forEach(doc => {
                        this.employee_id = doc.data().employee_id;
                        this.name = doc.data().name;
                        this.dept = doc.data().dept;
                        this.position = doc.data().position
                    })
                })
            },
            deleteEmployee() {
                db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(QuerySnapshot => {
                    QuerySnapshot.forEach(doc => {
                        doc.ref.delete();
                        this.$router.push('/')
                    })
                })
            }
        }

    }
</script>

