<template>
    <div id="new-employee" class="ma-3 pa-2">
        <h3>Add new Employee</h3>
        <v-form
                ref="form"
                v-model="valid"
                lazy-validation
        >
            <v-text-field
                    v-model="employee_id"
                    :rules="employee_idRules"
                    label="ID"
                    required
            ></v-text-field>
            <v-text-field
                    v-model="name"
                    :rules="nameRules"
                    label="Name"
                    required
            ></v-text-field>
            <v-text-field
                    v-model="dept"
                    :rules="deptRules"
                    label="Department"
                    required
            ></v-text-field>
            <v-text-field
                    v-model="position"
                    :rules="positionRules"
                    label="Position"
                    required
            ></v-text-field>

            <v-btn
                    color="gray"
                    @click="goToBack"
            >
                Go to back
            </v-btn>
            <v-btn
                    color="success"
                    @click="sendData"
            >
                Send
            </v-btn>
            <v-btn
                    color="error"
                    @click="reset"
            >
                Reset Form
            </v-btn>

        </v-form>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: "NewEmployee",

        data() {
            return {
                valid: true,

                employee_id: '',
                employee_idRules: [
                    v => !!v || 'ID is required',
                    v => /^[0-9]*$/.test(v) || 'ID must be only Numbers'
                ],

                name: '',
                nameRules: [
                    v => !!v || 'Name is required'
                ],

                dept: '',
                deptRules: [
                    v => !!v || 'Department is required'
                ],

                position: '',
                positionRules: [
                    v => !!v || 'Position is required'
                ],

            }
        },

        methods: {
            reset() {
                this.$refs.form.reset()
            },
            goToBack(){
                this.$router.push('/')
            },
            sendData(){
                db.collection('employees').add({
                    employee_id:this.employee_id,
                    name:this.name,
                    dept:this.dept,
                    position:this.position
                }).then(docRef =>{this.$router.push('/')})
                    .catch(error => {console.log(error)})
            }
        }
    }
</script>

