<template>
    <v-app>
        <div id="lateral">
            <v-toolbar dark flat color="pink">
                <v-layout justify-center>
                    <router-link
                            to="/"
                            tag="span"
                            style="cursor: pointer"
                            class="title"
                    > Employee Management </router-link>
                </v-layout>
            </v-toolbar>
            <router-view/>


        </div>
    </v-app>
</template>

<script>

    export default {
        name: 'App',
        data: () => ({
            fab: false,
            hidden: false,
            tabs: null
        }),

        computed: {
            activeFab() {
                switch (this.tabs) {
                    case 'one':
                        return {'color': 'indigo', icon: 'share'}
                    case 'two':
                        return {'color': 'red', icon: 'edit'}
                    case 'three':
                        return {'color': 'green', icon: 'keyboard_arrow_up'}
                    default:
                        return {}
                }
            }
        }
    }
</script>
